.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

html { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box;}

*, *:before, *:after {box-sizing: inherit}

.sr-only { position: absolute; width: 1px; height: 1px; margin: -1px; border: 0; padding: 0; white-space: nowrap; clip-path: inset(100%); clip: rect(0 0 0 0); overflow: hidden;}


/* general styling */
.wrapper {
  max-width: 1200px;
  width: 80%;
  margin: 0 auto;
}

html {
  font-size: 62.5%
}

body {
  background-image: url(./assets/body-background.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  font-family: 'Open Sans', sans-serif;
  background-attachment: fixed;
}

img {
  width: 100%;
  margin: 20px 0;
}

h1,
h2,
h3 {
  text-transform: uppercase;
  text-align: center;
}

h1 {
  font-size: 4rem;
  font-family: 'Fredoka One', cursive;
  color: antiquewhite;
  text-shadow: 0 0 10px black;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 3rem;
  color: antiquewhite;
}

p {
  font-size: 1.7rem;
}

/* buttons styling */
button {
  background: none;
  border: none;
}

.favBtn {
  font-size: 4rem;
  position: relative;
  top: 20px;
  color: #880114;
}

.favBtn:hover,
.favBtn:focus,
.favBtn:focus-within {
  cursor: pointer;
  color: #438926;
}

.favBtn:active {
  color: #255212;
}

.menuButtons {
  display: flex;
  justify-content: center;
}

.menuBtn {
  background-color: rgb(232, 200, 166);
  box-shadow: 0px 0px 10px white;
  font-size: 1.5rem;
  margin: 10px;
  padding: 10px;
  text-transform: uppercase;
  font-family: 'Raleway', sans-serif;
}

.menuBtn:hover,
.menuBtn:focus,
.menuBtn:focus-within {
  cursor: pointer;
  background-color: rgb(255, 198, 166);
  font-weight: bold;
}

.menuBtn:active {
  box-shadow: none;
}

.saveBtn {
  font-size: 4rem;
  position: absolute;
  top: 10%;
  right: 2%;
}

.plusBtn {
  color: #880114;
}

.checkBtn {
  color: #438926;
}

.saveBtn:hover,
.saveBtn:focus,
.saveBtn:focus-within {
  cursor: pointer;
}

/* name search form */
form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

input {
  font-size: 1.7rem;
  padding: 10px;
  text-align: center;
  margin: 20px;
}

/* drink box */
.drink {
  position: relative;
  display: flex;
  justify-content: space-evenly;
  margin: 20px 0;
  background-color: rgb(252, 247, 241);
}

.favDrink {
  width: 80%;
  margin: 20px auto;
}

.drinkImage {
  width: 15%;
  min-width: 200px;
  display: flex;
  align-items: center;
}

.drinkDescription {
  width: 60%;
}

.ingredients {
  font-size: 1.5rem;
  list-style-type: none;
  padding: 0;
}

.errorDescription {
  text-align: center;
}

/* animation */
.animateTop {
  animation: slideDown .5s ease-out;
}

@keyframes slideDown {
  0% { opacity: 0; transform: translateY(-25%); }   
  100% { opacity: 1; transform: translateY(0); }
}

.animateBottom {
  animation: slideUp .5s ease-out;
}

@keyframes slideUp {
  0% { opacity: 0; transform: translateY(25%); }   
  100% { opacity: 1; transform: translateY(0); }
}

@keyframes slideLeft {
  0% { opacity: 1; transform: translateX(100%); }   
  100% { opacity: 0; transform: translateX(0); }
}

@keyframes slideRight {
  0% { opacity: 0; transform: translateX(-100%); }   
  100% { opacity: 1; transform: translateX(0);}
}

.loadingAnimation {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
}

.loadingAnimation div {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin: 10px;
  background-color: #880114;
  animation: scaling 1s ease-in-out infinite;
}

@keyframes scaling {
  0%, 100% {
    transform: scale(.2);
  }

  50% {
    transform: scale(1);
  }
}

.loadingAnimation div:nth-child(1) {
  animation-delay: 0s;
}

.loadingAnimation div:nth-child(2) {
  animation-delay: .2s;
}

.loadingAnimation div:nth-child(3) {
  animation-delay: .4s;
}

.favMenu {
  width: 100vw;
  min-height: 100vh;
  position: absolute;
  left: -100%;
  top: 0;
  background-color: rgb(106, 2, 2);
  z-index: 2;
  background-size: contain;
}

.menuAnimationOn {
  animation: slideRight .5s ease-out;
  left: 0;
}

.menuAnimationOff {
  animation: slideLeft .5s ease-out;
  left: -100%;
}

.closeFavBtn {
  margin: 20px;
  font-size: 4rem;
  border: 2px solid white;
  color: white;
  display: inline-block;
  padding: 10px 20px;
  border-radius: 50%;
}

.closeFavBtn:hover,
.closeFavBtn:focus,
.closeFavBtn:focus-within {
  cursor: pointer;
}

/* mobile design */
@media (max-width: 720px) {
  .drink {
    flex-direction: column;
  }

  .drinkImage {
    width: 50%;
    margin: 20px auto 0 auto;
  }

  .drinkDescription {
    width: 100%;
    text-align: center;
  }

  .favMenu {
    width: 100vw;
  }
}